@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Play&display=swap');

* {
	box-sizing: border-box;
}

html {
	font-family: 'Play', sans-serif;
}

body {
	background-color: #002035;
	color: white;
	margin: 0;
	padding: 60px;
}

input {
	color: #7a7a7a;
	font-size: 1.2rem;
	width: 100%;
}

.card-grid {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}
.card {
	align-content: center;
	border: 5px solid red;
	border-radius: 5px;
	margin-bottom: 10px;
	margin-right: 10px;
	overflow: hidden;
}

.card p {
	padding: 5px;
	text-align: center;
}

.card img {
	align-items: center;
	max-height: 170px;
}
